import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ToastController, ViewController } from 'ionic-angular';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Http,RequestOptions, Headers } from '@angular/http';
import { ServiciosProvider } from '../../providers/servicios/servicios';
import { SpinnerProvider } from '../../providers/spinner/spinner'
import { Storage } from '@ionic/storage';
import { PerfilProvider } from '../../providers/perfil/perfil';
/**
 * Generated class for the PagotarjetaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-pagotarjeta',
  templateUrl: 'pagotarjeta.html',
})
export class PagotarjetaPage {


	private todo : FormGroup;
	id_servicio:any;

	tarjeta:any={};
  url:any;
  url_cargo:any;
  headers:any;
  data_cargo:any;
  token:any;
  headers_cargo:any;
  precio:any;
  descuento:any;
  total:any;
  email:any;
  data:any;

  constructor(private view:ViewController,private _perfil: PerfilProvider,private storage: Storage,public spinner: SpinnerProvider,private http: Http,public toastCtrl: ToastController,public _servicio:ServiciosProvider,private formBuilder: FormBuilder,public navCtrl: NavController, public navParams: NavParams) {


this.id_servicio = navParams.get("servicio");

    this._perfil.miperfil()
      .subscribe(data => {

          this.email=data[0]['email']


      })

this._servicio.detalleservicio(this.id_servicio)
             .subscribe(data => {


                 console.log('servicio...',data[0]['descuento'],data[0]['precio'])

               
                 this.precio = data[0]['precio']
                 this.descuento=data[0]['descuento']
                 this.total=this.precio-this.descuento
                 //this.total=this.total*100


           })


console.log(this.id_servicio)


     this.todo = this.formBuilder.group({
      cardnumber: ['', Validators.required],
      cvv: [''],
      expiration_month: [''],
      expiration_year:[''],
  
     

    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PagotarjetaPage');
  }


   agregatoast(data) {
    let toast = this.toastCtrl.create({
      message: data,
      duration: 4000,
      position:'top',
    });
    toast.present();
  }

  guardatarjeta(data){

    console.log(data)


    this._perfil.actualizatarjeta(data)
      .subscribe(data => {




    this.view.dismiss()


      })








  }

  closeModal(){

  
    this.view.dismiss()


}


  paga(datos){

    this.spinner.load();


    this.headers = {'Content-type': 'application/json', 'Authorization':'Bearer pk_live_IuiGmfK038sUfpJI'}

    this.headers_cargo = {'Content-type': 'application/json', 'Authorization':'Bearer sk_live_uHeesJg2yWNYnWtr'}


    this.data= {
          "card_number": datos.card_number,
          "cvv": datos.cvv,
          "expiration_month": datos.expiration_month,
          "expiration_year": datos.expiration_year,
          "email": this.email
        }


    this.url='https://api.culqi.com/v2/tokens'
    this.url_cargo='https://api.culqi.com/v2/charges'

      let options: RequestOptions = new RequestOptions({
      headers: new Headers(this.headers)
      });

      let options_cargo: RequestOptions = new RequestOptions({
      headers: new Headers(this.headers_cargo)
      });

      let creds = JSON.stringify(this.data);


      this.http.post(this.url, creds, options)
      .subscribe(
        data => {

          this.token = JSON.parse(data['_body'])['id']

         

          this.agregatoast('Estamos procesando tu pago')


                  ///Creando cargo

                  this.data_cargo = {

                      "amount": this.total,
                      "currency_code": "PEN",
                      "email": this.email,
                      "source_id":this.token
                  }


                  let cargo = JSON.stringify(this.data_cargo);

                  this.http.post(this.url_cargo, cargo, options_cargo)
                  .subscribe(
                  data => {

                       this.agregatoast('Conectando y sincronizando los servicios')

                           this._servicio.pagarenefectivo(this.id_servicio)

                              .subscribe(data => {

                                    this.navCtrl.popToRoot();

                                    this.storage.remove('pedido')

                                    this.agregatoast('Felicidades, se completo tu pago')
                                    this.spinner.dismiss();

                                  },
                                  error => {
                                  this.agregatoast(error._body)
                                  this.spinner.dismiss();
                                  })


                  },
                  error => {
                  this.agregatoast(error._body)
                  this.spinner.dismiss();
                  },

                  )





  

        },
          error => {
            this.agregatoast(error._body)
            this.spinner.dismiss();
          },

        )




  	// this._servicio.pagotarjeta(data,this.id_servicio)
   //           .subscribe(data => {


   //               console.log('data...',data)

   //               this.agregatoast(data)





             

   //         })




  }

}



