import { Component } from '@angular/core';
import { ToastController,IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { ServerProvider } from '../../providers/server/server';
import { ServiciosProvider } from '../../providers/servicios/servicios';
import { Http , Response } from '@angular/http';
import { HttpClient } from '@angular/common/http';
/**
 * Generated class for the FinalizaservicioPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-finalizaservicio',
  templateUrl: 'finalizaservicio.html',
})
export class FinalizaservicioPage {

		socia__photo:any;
	ped:any;
	fecha:any;
	fecha_inicio:any;
	foto:any;
	estado:any;

	host:any;
	codigo_servicio:any;
	socia:any;
	referencia:any;
	socia_photo:any;
	precio:any;
	nombre:any;
	todoser:any;
	ser:any;
	preciototal:any=0;
  precio_promo:any;
  servpedido:any;
  uno:any=false;
  dos:any=false;
  tres:any=false;
  cuatro:any=false;
  cinco:any=false;
  servic:any
  nota:any;


  constructor(public toastCtrl: ToastController,private view:ViewController,public _http: HttpClient,public server:ServerProvider,public _servicio:ServiciosProvider,public navCtrl: NavController, public navParams: NavParams) {


  	this.servpedido=this.navParams.get("servicio")

	this.host=this.server.getMyGlobalVar()

	console.log('calificando..')

	this._http.get(this.server.getMyGlobalVar()+'calificacion/'+this.servpedido)
     .subscribe(data => {


		
		this.socia=data[0]['servicio__socia__nombre']
		this.foto=data[0]['servicio__socia__photo']
		this.servic=data[0]['subcategoria__nombre']
console.log('servicos....',this.socia)

		})




  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FinalizaservicioPage');
  }

  califica(data){

  	this.nota=data

  	this.agregatoast('Perfecto, calificaste con '+data+ ' estrellas')

  	console.log(data)

  	if(data==1){
  		this.uno=true
  	}

  	if(data==2){
  		this.uno=true
  		this.dos=true
  	}

  	if(data==3){
  		this.uno=true
  		this.dos=true
  		this.tres=true
  	}

  	 if(data==4){
  		this.uno=true
  		this.dos=true
  		this.tres=true
  		this.cuatro=true
  	}

  	if(data==5){
  		this.uno=true
  		this.dos=true
  		this.tres=true
  		this.cuatro=true
  		this.cinco=true
  	}


  }

  descalifica(data){

  	  	this.agregatoast('Perfecto, calificaste con '+data+ ' estrellas')

  	this.nota=data

  	if(data==1){
  		this.uno=true
  		 this.dos=false
  		this.tres=false
  		this.cuatro=false
  		this.cinco=false
  	}

  	if(data==2){
  		this.uno=true
  		this.dos=true
  		this.tres=false
  		this.cuatro=false
  		this.cinco=false
  	}

  	if(data==3){
  		this.uno=true
  		this.dos=true
  		this.tres=true
  		this.cuatro=false
  		this.cinco=false
  	}

  	if(data==4){
  		this.uno=true
  		this.dos=true
  		this.tres=true
  		this.cuatro=true
  		this.cinco=false
  	}

  	if(data==5){
    	this.uno=true
  		this.dos=true
  		this.tres=true
  		this.cuatro=true
  		this.cinco=true
  	}

  }


  valida(){

  	console.log(this.nota)

  	if(this.nota){


  	this._http.get(this.server.getMyGlobalVar()+'califica/'+this.servpedido+'/'+this.nota)
     .subscribe(data => {

     		   this.view.dismiss()
		})


  	}
  	else{

  		this.agregatoast('Porfavor califica con las estrellas')



  	}



  }

  closeModal(){



    this.view.dismiss()


}


  agregatoast(data) {
    let toast = this.toastCtrl.create({
      message: data ,
      duration: 4000
    });
    toast.present();
  }


}



