  quitacarrito(data){



      const index: number = this.pedidos.indexOf(data);

      if (index !== -1) {



          this.precio=this.precio-this.pedidos[index].precio

          this.precio=Math.round(this.precio*100)/100

          
          this.pedidos.splice(index, 1);
      }  


  }


