import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PortadaProvider } from '../../providers/portada/portada';
import { ServiciosProvider } from '../../providers/servicios/servicios';

/**
 * Generated class for the NotificacionesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-notificaciones',
  templateUrl: 'notificaciones.html',
   providers: [PortadaProvider]
})
export class NotificacionesPage {


	notificaciones:any;

  constructor(private _portada: ServiciosProvider,public navCtrl: NavController, public navParams: NavParams) {



    this._portada.getnotificaciones()
            .subscribe(data => this.notificaciones = data);


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NotificacionesPage');
  }



}



