import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CategoriasProvider } from '../../providers/categorias/categorias';
/**
 * Generated class for the CoberturasPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-coberturas',
  templateUrl: 'coberturas.html',
})
export class CoberturasPage {

	distritos:any;

  constructor(private _categoria: CategoriasProvider,public navCtrl: NavController, public navParams: NavParams) {

  	    this._categoria.getdistritos()
            .subscribe(data => this.distritos = data);

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CoberturasPage');


  }



}



